<template>
  <div class="about"  ref=parentObjVue>
   <div class="comment">
                <component
                    v-for="(item,index) in comName"
                    :is="item.name"
                    :key="index"
                    :data="item.data"
                ></component>
   </div>
    <div id="in">
      <textarea class="putIn" maxlength="800"  placeholder="400" v-model="messData" rows="2">
      </textarea>
      <img class="sendI" src="../assets/fasong.png" width="25" height="25" @click="send">
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import commentS from '../pinglun/pinglun.vue'
import io from 'socket.io-client'
export default {
  data(){ 
        return{
            comName:[],
            messData:'',
            data:{
                comment: "", 
                floor: 1, 
                replies: [
                    "1111",
                    '1222'
                ]
            }
        }
    },
    methods:{
        send:function(){
            if(this.messData==""){
                this.$message({
                    message: '',
                    type: 'warning'
                });
            }
            else{
                this.$socket.emit("comment",{comment:this.messData});
                this.messData="";
            }
        },
        addCom:function(){
            this.comName.push({
                name:"commentS",
                data:this.data
            });
        },
    },
    components: { 
      commentS
    },
    sockets:{
        comment:function(data){
            console.log(data);
            this.data=data;
            this.addCom();
        },
        reply:function(data){
            console.log(data);
            location. reload();
        }
    },
    beforeCreate(){
       axios.get('http://47.107.243.207/api/getcomments')
            .then((response)=>{
                 console.log(response.data);
                let array=response.data;
                    for(let i=0;i<array.length;i++){
                        this.data=array[i];
                        this.addCom();
                    }
            })
            .catch(function(error){
                console.log(error);
            });
    }
}
</script>
